import React from 'react';
import Styled from 'styled-components';

const SectionBody = Styled.div`
    background-color: ${props => props.theme.sectionColor || props.theme.primaryColor || 'black'};
    color: ${props => props.theme.sectionTextColor || 'black'};
    position: relative;
    width: 100%;
`;

const Hr = Styled.span`
    margin: auto;
    display: block;
    width: 97%;
    border-top: 1px solid ${props => props.theme.sectionTextColor || 'black' }
`;

const SectionHeader = Styled.h1`
    text-align:center;
    padding-top: 25px;
    font-weight: 500;
`;

const PageSection = ({title, children, divider = true, ...props}) => {
    return (
        <SectionBody {...props}>
            <SectionHeader>{title}</SectionHeader>
            {divider &&
                <Hr/>
            }
            {children}
        </SectionBody>
    );
};

export default PageSection;


