import Styled, {css} from 'styled-components';


export default Styled.div`
    width: auto; 
    ${props => props.desktop && css`
        @media (min-width: 710px) {
            display: block;
        }
        @media (max-width: 710px) {
            display: none;
        }
    `}

    ${props => props.mobile && css`
        @media (min-width: 710px) {
            display: none;
        }
        @media (max-width: 710px) {
            display: block;
        }
    `}
`;


