import React from 'react';
import Styled from 'styled-components';

const Image = Styled.img`
    height: 100px;
    width: 100px;
    border-radius:70px;
    z-index:3000;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
`;

const RoundImage = ({src, ...props}) => {
    return(
        <Image src={src} {...props}/>
    )
};

export default RoundImage;


