import React from 'react';
import { Col } from 'react-grid-system';

const NoMarginCol = ({children, ...props}) => {
    return (
        <Col 
            style={{
                padding: 0,
                margin: 0
            }}
            {...props}
        >
            {children}
        </Col>
    )
};

export default NoMarginCol;



