import React from 'react';
import Styled from 'styled-components';

const DrawerBody = Styled.div`
    background-color: ${props => props.theme.drawerColor || props.color || 'red'};
    position: fixed;
    overflow-x: hidden;
    height: 100%;
    display: flex;
    flex-direction: column;
    z-index: 300;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    transition: all 0.5s;
`;

const DrawerLink = Styled.a`
    color: ${props => props.theme.drawerFontColor || 'black'};
    font-weight: 500;
    font-size: 36px;
    margin-top: 30px;
    padding: 8px 8px 8px 32px;
`;

const CloseButton = Styled.a`
    color: ${props => props.theme.drawerFontColor || 'black'};
    position: absolute;
    right: 0;
    margin-top: 15px;
    margin-right: 25px;
    font-size: 24px;
`;

const Drawer = (props) => {
    const {
        aboutClick,
        locationClick,
        contactClick,
        open,
        closeClick
    } = props;

    return (
        <DrawerBody 
            style={{
                width: open === true ? 274 : 0
            }}
        >
            <CloseButton onClick={closeClick}>X</CloseButton>
            <DrawerLink style={{ marginTop: 90 }} onClick={aboutClick}> Sobre </DrawerLink>
            <DrawerLink onClick={locationClick}>Localização</DrawerLink>
            {/*<DrawerLink>Cardápio</DrawerLink>*/}
            <DrawerLink onClick={contactClick}>Contato</DrawerLink>
        </DrawerBody>
    );
};

export default Drawer;


