import React from 'react';
import Styled from 'styled-components';

const Block = Styled.div`
    height: 300px;
    width: 100%;
    position: relative;
    margin-bottom: 10px;
    background-image: url(${props => props.src});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;  
    border-radius: 2px;
    text-align: center;
`;

const Title = Styled.h1`
    position: absolute;
    color: white;
    z-index: 3;
    padding: 20px;
    font-weight: bold;
    margin-top: 90px;
    padding-bottom: 10px;
`;

const Opacity = Styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.3;
    transition: all 0.2s linear;
    &:hover{
        opacity: 0.7;
    }
`;


const OpacityImage = ({src, title}) => {
    return (
        <Block src={src}>
            <Title>{title}</Title>
            <Opacity/>
        </Block>
    );
};

export default OpacityImage;


