import React from 'react';
import Styled, { css }from 'styled-components';

const InnerButton = Styled.div`
    ${props => props.leftBar && css`border-left: 1px solid ${props.theme.headerTextColor || `black`};`}
    font-size: 20px;
    font-weight: bold;
    padding: 10px;
    padding-right: 20px;
    padding-left: 20px;
    display: flex;
    flex-direction: row;
    
`;

const InnerButtonText = Styled.a`
    text-decoration: none;
    color: ${props => props.color || 'black'}
`;

const HeaderMenuItem = ({text, icon, leftBar, onClick, color}) => {
    return (
        <InnerButton 
            leftBar={leftBar} 
            color={color}
        >
            <span style={{marginRight: 12, fontSize: 24}}>
                {icon}
            </span>
            <InnerButtonText 
                color={color}
                onClick={onClick}
            >
                {text}
            </InnerButtonText>
        </InnerButton>
    );
};

export default HeaderMenuItem;


