import React from 'react';
import Slick from 'react-slick';
import Slide from '../Slide';
import './style.css';

let config = {
    centerMode: true,
    centerPadding: '10px',
    slidesToShow:3,
    responsive: [
        {
            breakpoint: 768,
            settings: {
                arrows: false,
                centerMode: true,
                centerPadding: '10px',
                slidesToShow: 3
            }
        },
        {
            breakpoint: 480,
            settings: {
                arrows: true,
                centerMode: true,
                centerPadding: '10px',
                slidesToShow: 1
            }
        }
    ]
};

const Slider = ({data, ...props}) => {
    return (
        <Slick {...config} {...props}>
            {data && data.map(slide => {
                return(
                    <Slide 
                        key={slide.src}
                        {...slide}
                    />
                );
            })}
        </Slick>
    );
};

export default Slider;


